import React from 'react';
import styled from 'styled-components';
import { CustomCheckbox, TodoBox } from './styles/todoStyles';
import { ChangeEvent, useState } from 'react';


import { RenderedEmoji } from './RenderedEmoji';
import { EmojiPicker } from './EmojiPicker';

import TextareaAutosize from 'react-textarea-autosize';

const CancelButton = styled.button`

  @media print { 
      display: none !important;
    }

  border: none;
  background: inherit;
  position: relative;
  height: 1.5em;
  width: 1.5em;
  cursor: pointer;
`;



export interface TODO {
    done: boolean;
    title: string;
    key: string;
    //isEdit: boolean;
    newIndex: number;
}

interface ITodoProps {
    index: number;
    todo: TODO;
    deleteTodo?(todo: TODO): void;
    changeTodo?(todo: TODO): void;
}

interface ITodoState {
}


export const ToDo = (props: ITodoProps, state: ITodoState) => {
    const textRef = React.createRef<HTMLTextAreaElement>();


    const addEmoji = (data: any): void => {
        const updatedTodo: TODO = { ...props.todo };
        updatedTodo.title += data.native;

        if (props.changeTodo) {
            props.changeTodo(updatedTodo);
        }

        setFocus();
    }

    const handleChangeCheckbox = (event: ChangeEvent<HTMLInputElement>) => {
        // 1. get current state and copy it
        const updatedTodo: TODO = { ...props.todo };

        updatedTodo.done = event.target.checked;

        // 3. write the update to state
        if (props.changeTodo) {
            props.changeTodo(updatedTodo);
        }
    }


    const handleChangeTextArea = (event: ChangeEvent<HTMLTextAreaElement>) => {
        // 1. get current state and copy it
        const updatedTodo: TODO = { ...props.todo };

        updatedTodo.title = event.target.value;

        // 3. write the update to state
        if (props.changeTodo) {
            props.changeTodo(updatedTodo);
        }

        console.log(updatedTodo.title.length);
    }



    const [refresh, doRefresh] = useState(false);
    const onMouseLeave = () => {

        doRefresh(!refresh);
    }


    const onKeyPress = (event: React.KeyboardEvent<HTMLTextAreaElement>) => {
        //console.log(event.key);
        if (event.key === 'Enter') {
            event.preventDefault();
            event.stopPropagation();
        }
    };

    const setFocus = () => {
        if (textRef.current) {
            const element = textRef.current;

            //console.log("onFocus", element.selectionStart, element.selectionEnd);

            element.focus();
            element.setSelectionRange(element.value.length,element.value.length);
        }
    };

    
    return (
        <TodoBox done={props.todo.done} index={props.index} onMouseLeave={onMouseLeave}>

            <CustomCheckbox index={props.index}>
                <input type="checkbox" name={props.todo.key} id={props.todo.key} checked={props.todo.done} data-testid="todo-checkbox" onChange={handleChangeCheckbox} />

                <label htmlFor={props.todo.key}>
                    <span>âœ“</span>
                </label>
            </CustomCheckbox>
            
            <TextareaAutosize name="todo" id="todo" ref={textRef} data-testid="todo-input" onClick={(e) => e.stopPropagation()}
                    onKeyPress={onKeyPress}
                    onChange={handleChangeTextArea}
                    value={props.todo.title}
                    style={{ maxWidth: (props.todo?.title?.length * 0.7) + "em" }} />
           

            <RenderedEmoji value={props.todo.title} isDone={props.todo.done} />

            <EmojiPicker addEmoji={addEmoji} refresh={refresh} />

            <CancelButton data-testid="cancel-todo" onClick={() => {
                if (props.deleteTodo) {
                    props.deleteTodo(props.todo);
                }
            }}>

                <img src='images/icon-cross.svg' alt="cancel"></img>
            </CancelButton>
        </TodoBox>
    )

}
