import styled from 'styled-components';
//import {Theme} from './styles/theme';
import DatePicker from "react-datepicker";

import "react-datepicker/dist/react-datepicker.css";

import ReactGA from 'react-ga';

import { useState } from 'react';

import { format } from 'date-fns';

const DatePickerWrapper = styled.div`
    align-self: center;

    .react-datepicker {
        position: absolute;
        z-index: 100;
    }

    .example-custom-input {
        cursor: pointer;
        padding: 5px 15px;
        border: 0;
        border-radius: 4px;
        background: transparent;
        font: inherit;
        color: #fff;
    }
`

const StyledHeader = styled.div`

    
  display: flex;
  justify-content: space-between;
  align-content: center;

    h1 {
        
        color: ${props => props.theme.headerColor};
        font-weight: 700;
        text-transform: uppercase;
        font-size: 1rem;
        letter-spacing: 0.3em;
        margin: 1em 0;
    }

    span {

        display: flex;
        align-items: center;
        justify-content: center;
        align-self: center;

        height: 30px;

        img { 
            height: 30px;
            margin-right: 5px;
        }

        span {
            border-bottom: dotted 1px #f6f6ff;
            color: #f6f6ff;
            font-weight: bold;
        }

    }

    .theme-changer {
        border: none;
        background: transparent;
        cursor: pointer;
    }
    
  
    @media print { 
        justify-content: right;

        h1, button, img, span, .theme-changer, .react-datepicker {
            display: none;
        }

        .example-custom-input {
            
            display: block !important;

                margin-top: -30px;
                align-self: flex-end;
                color: black;
                float: right;
        }
    }

`;



export const ListHeader = (props: any) => {
    const [startDate, setStartDate] = useState(new Date());
    const [isOpen, setIsOpen] = useState(false);

    const formatString = "MMMM d, yyyy";

    const handleChange = (date: Date) => {
        setIsOpen(!isOpen);
        setStartDate(date);
    };
    const handleClick = (e: any) => {
        e.preventDefault();
        setIsOpen(!isOpen);
    };


    const printPage = () => {
        window.print();

        try {
            ReactGA.event({
                category: 'User',
                action: 'Printed tasks'
            });
        } catch (e) {

        }
    };


    return (
        <StyledHeader className="header">
            <h1>Print your tasks</h1>

            <DatePickerWrapper>

                <button className="example-custom-input" onClick={handleClick}>{format(startDate, formatString)}</button>

                {isOpen ?

                    <DatePicker className=".datepicker"
                        inline
                        dateFormat={formatString}
                        onChange={handleChange}
                    />

                    : null}



            </DatePickerWrapper>

            <span className="theme-changer" onClick={printPage}>
                <img alt="theme-changer" src="./logo192.png">
                </img>
                <span>Print</span>
            </span>
        </StyledHeader>
    )

}
