import React from 'react';
import styled from 'styled-components';
import { CustomCheckbox } from './styles/todoStyles';
import {FormEvent, useState} from "react";
import {TODO} from "./ToDo";
import { EmojiPicker } from './EmojiPicker';


import TextareaAutosize from 'react-textarea-autosize';

const InvisibleSubmit = styled.button`
    visibility: hidden;
    display: none;
`;

// there is little difference between the TodoBox and this - can I maybe extend the todoBox styles somehow?
const StyledInputForm = styled.form`
    background-color: ${props => props.theme.todoBackgroundColor};
    padding: 1em;
    grid-gap: 1em;
    display: grid;
    grid-template-columns: auto minmax(0, 1fr) auto;
    align-items: center;
    justify-content: center;
    margin-bottom: 1.5em;
    border-radius: 0.3em;

    input, textarea {
        font: inherit;
        background: inherit;
       
        color: ${props => props.theme.todoTextColor};


        border: none;
        overflow: auto;
        outline: none;
    
        -webkit-box-shadow: none;
        -moz-box-shadow: none;
        box-shadow: none;
    
        resize: none; /*remove the resize handle on the bottom right*/
  }

  @media(max-width: 650px) {
    font-size: 0.8rem;
  }

  @media print
  { 
  display: none !important;

  }
`;



interface IInputFormProps {
    addTodo(todo: TODO): void;
  }
  /*
  interface IInputFormState {
  }
*/

//class InputForm extends React.Component<IInputFormProps, IInputFormState> {

    
export const InputForm = (props: IInputFormProps) => {

    const submitRef = React.createRef<HTMLButtonElement>();
    const textRef = React.createRef<HTMLTextAreaElement>();
    const doneRef = React.createRef<HTMLInputElement>();
    
    const createTodo = (event: FormEvent<HTMLFormElement>): void => {
        event.preventDefault();

        const value = textRef?.current?.value || "";

        if (value === "") {
            return;
        }

        const todo: TODO = {
            key: Date.now().toString(),
            title: value,
            done: doneRef?.current?.checked || false,
            //isEdit: false,
            newIndex: -1
        }
        props.addTodo(todo);
        event.currentTarget.reset();

        if (textRef?.current) {
            textRef.current.value = "";
        }

    }

    const addEmoji = (data: any): void => {
        if (textRef?.current) {
            textRef.current.value += data.native;

            textRef.current.focus();
        }
    }
    
  
    const [refresh, doRefresh] = useState(false);
    const onMouseLeave = () => {

        doRefresh(!refresh);
    }

    const onKeyPress = (event: React.KeyboardEvent<HTMLTextAreaElement>) => {
        //console.log(event.key);
        if (event.key === 'Enter') {
            submitRef.current?.click();
            event.preventDefault();
            event.stopPropagation();
        }
    };

    return (
        
        <StyledInputForm onSubmit={createTodo} onMouseLeave={onMouseLeave}>
            <CustomCheckbox index={-1}>
                <input type="checkbox" name="done" id="done" ref={doneRef} />
                <label htmlFor="done">
                    <img src={process.env.PUBLIC_URL + "/images/icon-check.svg"} alt=""/>
                </label>
            </CustomCheckbox>

            <TextareaAutosize name="todo" id="todo" ref={textRef} data-testid="todo-input" onKeyPress={onKeyPress}/>

 
            <EmojiPicker addEmoji={addEmoji} refresh={refresh}/>
            
            <InvisibleSubmit type="submit" ref={submitRef}></InvisibleSubmit>
        </StyledInputForm>

    )
    
}

export default InputForm;